// import { apiSearchRecord } from '../../service/index'
export default {
	data() {
		return {
			recordList: ['美食', '护肤', '生活百货', '巧克力'],
			searchList: ['美食', '护肤', '生活百货', '巧克力', '口红', '运动衫', '手表']
		}
	},
	onShow() {
		// this.getSearchList()
	},
	methods: {
		handleBack() {
			uni.navigateBack()
		},
		getSearchList() {
			apiSearchRecord()
				.then((res) => {
					if (res.code == 200) {
						this.recordList = res.data
					}
				})
		},
		deleteRecord() {
			this.recordList = []
		},
		searchPage(item) {
			uni.navigateTo({
				url: '/pages/shop-list/shop-list?keyword=' + item
			});
		},
		search() {
			uni.navigateTo({
				url: '/pages/shop-list/shop-list'
			});
		}
	}
}
